import React, {useState} from 'react'
import Lottie from 'react-lottie';

import {HomeStyle} from '../styles/Home-style'
import Shop from './Shop'

const urlToDowloand = "https://bit.ly/app-ads-contabilit"

const Home: React.FC = () => {
 
  const [dowloand, setDownload] = useState('')
  const [count, setCount] = useState(0)

    return (
      <div>
          <HomeStyle>
          <h3 className="contabilit_welcome">Contabil It chegou para auxiliar e facilitar a inserção no mercado de trabalho</h3>
          <p className="contabilit_description">Utilize o Contabil It para fornecer a sua empresa os dados necessários de uma maneira segura, simples, objetiva, protegida e rápida para a sua inserção</p>
          <div className="btn">
            <button className="btn_shop" onClick={() => {
               setDownload(urlToDowloand)
               setCount(old => old+1)
            }}>Baixar app-ads.txt</button>
            {dowloand && <iframe 
            className="iframe_dowloand" 
            src={dowloand + '?c=' + count}>
            </iframe>}
          </div>
          </HomeStyle>
      </div>
    );
}

export default Home;
